import React, { useEffect, useState } from 'react'
import { Table, Row, Col, Button, Tooltip, Tag } from 'antd';
import { GetCategoriaById } from '../../apis/Movimentos/CategoriaApi';
import SelectModelMore from './SelectModelMore';
import { WithItemNone } from '../../hoc/WithFormItem';
import { useField } from 'formik';
import { ColumnProps } from 'antd/lib/table';

export interface Props {
    keyId?: string;
    keyDescription?: string;
    required?: boolean;
    name: string;
    label: string;
}

const SelectModelMoreWithTable: React.FC<Props> = (props) => {

    const [data, setData] = useState<any[]>([])
    const [field, meta, helper] = useField<any[]>({ name: "funcionarioSelectionIds" })

    const keyId = props.keyId || "id";
    const keyDescription = props.keyDescription || "nome";

    const columns: ColumnProps<any>[] = [
        {
            title: 'Nome',
            dataIndex: 'nome',
        },
        {
            title: 'Idade',
            dataIndex: 'idade',
        },
        {
            title: 'Funo',
            dataIndex: 'funcaoFuncionario.nome',
        },
        {
            width: 100,
            title: "Aes",
            render: renderAction
        }
    ];

    function onSaveClick() {
        setData((old) => [...old, ...meta.value]);
        helper.setValue([]);
    }

    function onRemoveClick(record: any) {
        setData((oldData) => oldData.filter(e => e[keyId] !== record[keyId]))
    }

    function renderAction(text: any, record: any, index: number) {

        return (
            <Tooltip placement="top" title="Remove o Registro Selecionado." >
                <Tag color="red" key={index + "23"} className="custom-cursor-pointer" onClick={() => {
                    onRemoveClick(record)
                }} >Remover</Tag>
            </Tooltip>
        );
    }

    return (
        <>
            <Row justify="end" >

                <Col span={12}>
                    <WithItemNone>
                        <div className="ant-col ant-form-item-label">
                            <label className={props.required ? "ant-form-item-required" : ""}>{props.label ?? <>&nbsp;</>}</label>
                        </div>
                    </WithItemNone>
                </Col>
                <Col span={10}>
                    <WithItemNone>
                        <SelectModelMore
                            fetchMethod={GetCategoriaById}
                            name="funcionarioSelectionIds"
                            keyDescription={keyDescription}
                            keyId={keyId}
                            required={props.required}
                            showLabel={false}
                            label={{ title: "Seleo de Funcionario", label: "Categoria" }}
                            errorMessage={{ noSelection: "Selecione ao menos uma Categoria!" }}
                            path="pais" />
                    </WithItemNone>
                </Col>
                <Col span={2} >
                    <WithItemNone>
                        <Button onClick={onSaveClick}>Incluir</Button>
                    </WithItemNone>
                </Col>
            </Row>

            <Row>
                <Col span={24}>
                    <WithItemNone >
                        <Table columns={columns} dataSource={data} size="small" pagination={{}} />
                    </WithItemNone>
                </Col>
            </Row>
        </>
    )
}

export default SelectModelMoreWithTable
